import { Box, lighten, styled } from "@rp/react/web/mui/core"

const FINANCE_TABLE_ROW_CLASS = "table-row"
const FINANCE_TABLE_CELL_CLASS = "table-cell"
const FINANCE_TABLE_HEAD_CLASS = "table-head"
const FINANCE_TABLE_BODY_CLASS = "table-body"
const FINANCE_TABLE_SCROLLED_HORIZONTALLY_CLASS = "scrolledHorizontally"
const FINANCE_TABLE_ITEM_PREFIX = "finance-table-item-"

const WatchlistDataTabsTableContainer = styled(Box, {
  shouldForwardProp: prop => prop !== "showGradient",
})<{ showGradient?: boolean }>(({ theme, showGradient }) => ({
  width: "fit-content",
  minWidth: "100%",
  [`& .${FINANCE_TABLE_ROW_CLASS}`]: {
    minHeight: 44,
    position: "relative",
    display: "grid",
    alignItems: "stretch",
    borderBottom: `1px solid ${theme.palette.divider}`,
    [`& .${FINANCE_TABLE_CELL_CLASS}`]: {
      height: "100%",
      display: "flex",
      alignItems: "center",
    },
    [`& .${FINANCE_TABLE_CELL_CLASS} > *`]: {
      height: "100%",
      display: "flex",
      alignItems: "center",
    },
    "&:before": {
      content: '""',
      position: "sticky",
      left: 0,
      top: 0,
      width: "3px",
      zIndex: 10,
      height: "100%",
      backgroundColor: theme.palette.background.default,
    },
  },
  [`& .${FINANCE_TABLE_ROW_CLASS} .${FINANCE_TABLE_CELL_CLASS}:nth-of-type(2)`]: {
    zIndex: 10,
  },
  [`& .${FINANCE_TABLE_ROW_CLASS} .${FINANCE_TABLE_CELL_CLASS}:nth-of-type(2):after`]: {
    content: showGradient ? '""' : undefined,
    position: "absolute",
    right: -129,
    top: 0,
    width: "130px",
    zIndex: 10,
    height: "100%",
    background: `linear-gradient(90deg, #121212 16.07%, rgba(18, 18, 18, 0.00) 94.2%)`,
    borderLeft: `1px solid ${theme.palette.divider}`,
    pointerEvents: "none",
    opacity: 0,
    transition: "opacity 0.2s ease-in-out",
  },
  [`.${FINANCE_TABLE_SCROLLED_HORIZONTALLY_CLASS} & .${FINANCE_TABLE_ROW_CLASS} .${FINANCE_TABLE_CELL_CLASS}:nth-of-type(2):after`]:
    {
      opacity: 1,
    },
  [`& .${FINANCE_TABLE_ROW_CLASS}:hover .${FINANCE_TABLE_CELL_CLASS}:first-of-type`]: {
    cursor: "pointer",
  },
  [`& .${FINANCE_TABLE_BODY_CLASS} .${FINANCE_TABLE_ROW_CLASS}:hover .${FINANCE_TABLE_CELL_CLASS}`]:
    {
      backgroundColor: lighten(theme.palette.background.paper, 0.1),
      cursor: "pointer",
    },
  [`& .${FINANCE_TABLE_BODY_CLASS} .${FINANCE_TABLE_ROW_CLASS}:hover`]: {
    backgroundColor: lighten(theme.palette.background.paper, 0.1),
    cursor: "pointer",
    "&:before": {
      backgroundColor: lighten(theme.palette.background.paper, 0.1),
    },
  },

  [`& .${FINANCE_TABLE_BODY_CLASS} .${FINANCE_TABLE_ROW_CLASS} .${FINANCE_TABLE_CELL_CLASS}`]: {},
  [`& .${FINANCE_TABLE_BODY_CLASS} .${FINANCE_TABLE_ROW_CLASS}:hover .hover-action`]: {
    opacity: 1,
  },
  [`& .${FINANCE_TABLE_BODY_CLASS} .${FINANCE_TABLE_ROW_CLASS}.selected`]: {
    backgroundColor: lighten(theme.palette.background.paper, 0.1),
    cursor: "pointer",
    position: "relative",
    "&:before": {
      backgroundColor: theme.palette.primary.main,
    },
  },
  [`& .${FINANCE_TABLE_BODY_CLASS} .${FINANCE_TABLE_ROW_CLASS}.selected .${FINANCE_TABLE_CELL_CLASS}`]:
    {
      backgroundColor: lighten(theme.palette.background.paper, 0.1),
      cursor: "pointer",
    },
  [`& .${FINANCE_TABLE_HEAD_CLASS} .${FINANCE_TABLE_CELL_CLASS}`]: {
    paddingLeft: theme.spacing(1.2),
    paddingRight: theme.spacing(1),
    paddingTop: theme.spacing(1.5),
    paddingBottom: theme.spacing(1.5),
    fontSize: 12,
    fontWeight: "400",
  },
  [`& .${FINANCE_TABLE_HEAD_CLASS} .${FINANCE_TABLE_CELL_CLASS} > *`]: {
    alignItems: "center",
  },
  [`& .${FINANCE_TABLE_HEAD_CLASS} .${FINANCE_TABLE_CELL_CLASS} > * > svg`]: {},
}))

export {
  FINANCE_TABLE_BODY_CLASS,
  FINANCE_TABLE_CELL_CLASS,
  FINANCE_TABLE_HEAD_CLASS,
  FINANCE_TABLE_ROW_CLASS,
  FINANCE_TABLE_SCROLLED_HORIZONTALLY_CLASS,
  FINANCE_TABLE_ITEM_PREFIX,
  WatchlistDataTabsTableContainer,
}
